import React, { Component } from 'react';
import TodoForm from './todoform';
import Todo from './todo';

class TodoList extends Component {
    state = {
        todos: [],
        complete: false,
        s: 'all',
    }
    render() {
        let todos = this.state.todos.filter(todo => {
            if (this.state.s === 'all') {
                return todo;
            }
            else if (this.state.s === 'completed') {
                if (todo.complete) return todo;
            }
            else {
                // displat the one which are not completed
                if (!todo.complete) return todo;
            }
        })
        return (
            <div>

                <div>
                    <h3 className="text text-center" style={{ fontWeight: 'bold' }}>todos left :  {this.state.todos.filter(todo => !todo.complete).length}</h3>
                    <br /><br /><br />
                </div>
                {/* tryig to make it on middle */}
                <div style={{ display: 'flex', justifyContent: 'center' }}>
                    <div>
                        <TodoForm onSubmit={this.handleSubmit} />
                        {todos.map(todo => (
                            <div>
                                <Todo key={todo.id} onclick={() => this.handleClick(todo.id)} req={todo} />
                            </div>
                        ))}
                    </div>

                    <div className="" >
                        <button style={{ margin: 5 }} className="btn btn-secondary" onClick={this.handleAll}> all</button>
                        <button style={{ margin: 5 }} className="btn btn-success" onClick={this.handleA} > active</button>
                        <button style={{ margin: 5 }} className="btn btn-info" onClick={this.handleC}>completed</button>
                        <button style={{ margin: 5 }} className="btn btn-primary" onClick={() => this.setState({
                            todos: this.state.todos.filter(todo => {
                                todo.complete = true;
                                return todo;
                            })
                        })}>complete all</button>
                        <button className="btn btn-danger" onClick={() => this.setState({
                            todos: this.state.todos.filter(todo => {
                                if (!todo.complete) return todo;
                            })
                        })}> deleteComplete</button>
                    </div >
                </div>
            </div >
        );
    }

    handleAll = () => {
        // chnage the state of s
        this.setState({
            s: 'all'
        })
    }
    handleA = () => {
        // chnage the state of s
        this.setState({
            s: 'active'
        })
    }
    handleC = () => {
        // chnage the state of s
        this.setState({
            s: 'completed'
        })
    }


    handleClick(id) {
        // make the things strike off
        this.setState({
            todos: this.state.todos.filter(todo => {
                if (todo.id == id) {
                    todo.complete = !todo.complete;
                    return todo;
                } else {
                    return todo;
                }
            })
        })
    }

    handleSubmit = (todo) => {
        let dup = this.state.todos;
        dup.push(todo);
        this.setState({
            todos: dup,
        })
    }

}

export default TodoList;