import React, { Component } from 'react';


class Todo extends Component {
    state = {}
    render() {
        return (
            <div onClick={this.props.onclick} style={{
                textDecoration: this.props.req.complete ? "line-through" : "",
            }}>
                {this.props.req.text}
            </div>
        );
    }
}

export default Todo;