import React, { Component } from 'react';
import 'shortid';
import shortid from 'shortid';

class TodoForm extends Component {
    state = {
        text: '',
    }
    render() {
        return (
            <div>
                <textarea type="text" value={this.state.text} type="text" placeholder="enter what u want to do ❤️" onChange={this.handleChange} />
                <button type='submit' className="btn btn-warning" onClick={this.handleSubmit}>submit</button>
            </div>
        );
    }

    handleSubmit = () => {
        this.props.onSubmit({
            text: this.state.text,
            complete: false,
            id: shortid.generate(),
        });

        this.setState({
            text: '',
        })
    }

    handleChange = (event) => {
        this.setState({
            text: event.target.value,
        })
    }

}

export default TodoForm;